<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
      <v-divider class="mx-4" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="Drink Name"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.calories" :items="item" label="Type"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>

    <v-col v-for="drinks in drinks" :key="drinks">
      <v-card class="mx-auto" max-width="344" outlined>
        <v-list-item three-line>
          <v-list-item-content>
            <div class="overline mb-4">Minuman</div>
            <v-list-item-title class="headline mb-1">{{drinks.name}}</v-list-item-title>
            <v-list-item-subtitle>{{drinks.calories}}</v-list-item-subtitle>
            <v-list-item-subtitle>{{drinks.fat}}</v-list-item-subtitle>
            <v-list-item-subtitle>{{drinks.carbs}}</v-list-item-subtitle>
            <v-list-item-subtitle>{{drinks.protein}}</v-list-item-subtitle>
            <v-list-item-subtitle>{{drinks.actions}}</v-list-item-subtitle>
            <v-card-actions>
              <v-btn class="mr-1" @click="editItem(drinks)" color="success">Edit</v-btn>
              <v-btn class="mr-1" @click="deleteItem(drinks)" color="error">Delete</v-btn>
            </v-card-actions>
          </v-list-item-content>
        </v-list-item>
      </v-card>
    </v-col>
  </div>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>
<script>
import { drinksRef } from "../firebase";

export default {
  data: () => ({
    search: "",
    dialog: false,
    cek: -1,
    empty: -1,
    checked: [],
    item: ["Ice", "Warm"],
    headers: [
      {
        text: "Drinks (100g serving)",
        align: "left",
        sortable: false,
        value: "name"
      },

      {
        text: "Type",
        value: "calories"
      },

      {
        text: "Fat (g)",
        value: "fat"
      },

      {
        text: "Carbs (g)",
        value: "carbs"
      },

      {
        text: "Protein (g)",
        value: "protein"
      },

      {
        text: "Actions",
        value: "action",
        sortable: false
      }
    ],
    drinks: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0
    },
    defaultItem: {
      name: "",
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0
    }
  }),

  firebase: {
    drinks: drinksRef
  },

  computed: {
    formTitle() {
      return this.cek === -1 ? "New Item" : "Edit Item";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    close() {
      this.dialog = false;
    },
    // methods untuk save
    save() {
      if (this.cek > -1) {
        drinksRef.child(this.editedIndex).set({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein
        });
        this.clear();
      } else {
        drinksRef.push({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein,
          checkbox: (this.checked = [])
        });
        this.clear();
      }
      this.close();
    },

    editItem(item) {
      this.editedIndex = item[".key"];
      this.cek = 0;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      confirm("Are you sure you want to delete this item?") &&
        drinksRef.child(item[".key"]).remove();
    },

    clear() {
      this.editedItem = {};
      this.editedItem.name = "";
      this.editedItem.calories = 0;
      this.editedItem.fat = 0;
      this.editedItem.carbs = 0;
      this.editedItem.protein = 0;
      this.cek = -1;
    }
  }
};
</script>
